.question_13 {
    .question-container {
        text-align: center;
        margin-top: 20px;

        h2{
            text-align: center;
            font-weight: 400;
            color: #fefefe;
            width: 75%;
            justify-self: anchor-center;
          }
    
          p {
            color: #fefefe;
          }
    }
    
    .options-list {
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: center;
        margin-top: 20px;
        gap: 25px;

        @media (max-width: 1024px) {
            flex-wrap: wrap;
        }


        .opt-btn-img {
            border: 1px solid #484848;
            border-radius: 10px;
            margin: 30px 0;
            img {
                height: 250px;
                width: 250px;

                @media (max-width: 1024px) {
                    height: 250px;
                    width: 250px;
                }
            }
            &.selected {
                background-color: #f6b52e;
            }

            &:hover {
                cursor: pointer;
            }
        }
    }
    
    .option {
        font-size: 1.2rem;
        display: flex;
        align-items: center;
        gap: 10px;
    }

    .submit-btn {
        padding: 10px 20px;
        background-color: #3564ab;
        color: #fff;
        text-transform: uppercase;
        border: none;
        border-radius: 10px;
        font-size: 20px;
        cursor: pointer;
        margin-top: 60px;
        width: fit-content;
        margin: 30px auto;
    
        &:hover {
            background-color: #28a745;
        }
      }
      
      .submit-btn:disabled {
        background-color: #ccc;
        cursor: not-allowed;
      }
}


